@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@0,100;0,200;0,300;0,600;0,700;1,100;1,200;1,300;1,600;1,700&display=swap");

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* width: 1440px;
  height: 2477px; */
}

/* code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
} */

/* Navbar CSS */

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-evenly !important;
  min-height: 72px;
  /* padding: 10px; */
}

.search-bar {
  width: fit-content;
  text-align: center;
}

.rounded-input {
  border-radius: 21px;
  background: #f2f2f2;
  border: none;
  width: 320px;
  height: 42px;
  text-align: center;
  color: #5c5c5c;
  font-size: 14px;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: 500;
}

.searchIcon {
  position: relative;
  top: 0;
  left: 40px;
}

.create-account {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #000;
  font-size: 14px;
  color: #2e2e2e;
  text-align: center;
  font-size: 16px;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: 500;
  padding-right: 72px;
}

.create-account a {
  text-decoration: none;
  color: #2f6ce5;
  font-size: 16px;
}

/* Home Banner CSS */

.homeBannerDiv {
  min-height: 440px;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("/public/images/homeBanner.png") 50% center / cover no-repeat;
}

.bannerHeading {
  color: #fff;
  font-size: 36px;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: 700;
  padding-top: 216px;
  /* top: 386px; */
}

.bannerSubheading {
  color: #fff;
  font-size: 18px;
  font-family: "IBM Plex Sans", sans-serif;
  margin-top: -20px;
  /* top: 437px; */
}

/* Menu Bar CSS */

.menuBar {
  box-sizing: border-box;
  border-bottom: 1px solid #8a8a8a;
  /* z-index: -1; */
}

.nav-link {
  text-decoration: none;
  color: #8a8a8a;
}

.nav-link:hover {
  text-decoration: none;
  color: #000000;
}

.mobilePosts{
  color: #212529;
  font-size: 14px;
  font-family: IBM Plex Sans;
  font-weight: 700;
  letter-spacing: 0.14px;
}


/* Post CSS */

.viewAndShare {
  color: #525252;
  text-align: right;
  font-size: 14px;
  font-family: "IBM Plex Sans";
  font-weight: 500;
}

.shareButton {
  border-radius: 2px;
  background: #edeef0;
}
.shareButtonMobile {
  border-radius: 2px;
  background: #edeef0;
  margin: -58px 0 0 0;
}

.mobileViews {
  margin: -16px 0 0 62px;
  color: #495057;
  font-size: 13px;
  font-family: IBM Plex Sans;
  font-weight: 500;
}

.cardMobile{
  border: none;
}
